import LoginForm from "@/components/LoginForm";
import { getServerApiBase } from "@/lib/api";
import { redirect } from "next/navigation";
import { buildServerCookieHeader } from "@/lib/server/cookies";

async function ensureLoggedOut() {
  const cookieHeader = await buildServerCookieHeader();
  if (!cookieHeader) return;
  const res = await fetch(`${getServerApiBase()}/me`, {
    headers: { Cookie: cookieHeader },
    cache: "no-store",
  }).catch(() => null);
  if (res?.ok) redirect("/");
}

export default async function LoginPage() {
  await ensureLoggedOut();

  return (
    <div className="relative min-h-screen overflow-hidden bg-gradient-to-br from-[#FDF5FF] via-[#FDEAFE] to-[#F3F2FF] dark:from-[#09031a] dark:via-[#120825] dark:to-[#080516] px-4 py-10">
      <div className="pointer-events-none absolute -top-24 -left-10 h-72 w-72 rounded-full bg-[#E9A5F1]/50 blur-3xl" />
      <div className="pointer-events-none absolute top-40 -right-20 h-80 w-80 rounded-full bg-[#8F87F1]/40 blur-3xl" />
      <div className="relative z-10 mx-auto grid w-full max-w-5xl gap-10 rounded-[36px] bg-white/70 p-8 shadow-2xl ring-1 ring-white/50 backdrop-blur-2xl dark:bg-slate-900/60 dark:ring-slate-800 md:grid-cols-2">
        <div className="space-y-6 text-left">
          <div className="inline-flex items-center gap-2 rounded-full border border-white/60 px-3 py-1 text-xs font-semibold uppercase tracking-[0.3em] text-slate-500 dark:text-slate-200">
            Community savings
          </div>
          <h1 className="text-4xl font-semibold leading-tight text-slate-900 dark:text-white">
            Manage your circle’s money with clarity and calm.
          </h1>
          <p className="text-base text-slate-500 dark:text-slate-300">
            Track deposits, dues, and investments in a single shared space. Beautiful insights without spreadsheets.
          </p>
          <div className="grid gap-4 text-sm">
            {[
              "Instant snapshots of balance & promises",
              "Yearly collection reports in one tap",
              "Role-aware access for every member",
            ].map((item) => (
              <div key={item} className="flex items-center gap-3 rounded-2xl bg-gradient-to-r from-[#E9A5F1]/30 to-[#C68EFD]/30 px-4 py-3 text-slate-700 dark:text-slate-100">
                <span className="text-lg">✨</span>
                <span>{item}</span>
              </div>
            ))}
          </div>
        </div>
        <div className="flex items-center justify-center">
          <div className="w-full max-w-md space-y-6 rounded-[28px] border border-[#E9A5F1]/60 bg-white/80 p-6 shadow-xl ring-1 ring-white/70 backdrop-blur-xl dark:border-slate-800 dark:bg-slate-900/70 dark:ring-slate-900">
            <div className="text-center">
              <p className="text-xs uppercase tracking-[0.4em] text-slate-400">Sign in</p>
              <h2 className="mt-1 text-2xl font-semibold text-slate-900 dark:text-white">Welcome back</h2>
              <p className="text-sm text-slate-500 dark:text-slate-400">Enter your credentials to continue.</p>
            </div>
            <LoginForm />
          </div>
        </div>
      </div>
    </div>
  );
}
